        role="tabpanel"
      >
        <h4 class="font-weight-bold w-100 border-bottom pb-3 mb-3"></h4>
        <h5 class="font-weight-bold w-100 border-bottom pb-3 mb-3"></h5>
        <div class="mb-40">
          <p class="d-flex  flex-wrap align-items-center tabSubTitle  mb-2">
            <span class="text-primary font-weight-bold mr-0">()</span>
          </p>
          <textarea
            :disabled="method.indexOf('view') >=0"
            cols="30"
            rows="10"
            class="form-control border-camera w-75 mt-4"
            disabled
          >
            () ()() ()() ()  : ()() () () 146 ()()() ()  () () ()() ; () () ()() () () () ()( ) ______________________________________________       
          </textarea>
        </div>
        <!--  -->
        <h4 class="font-weight-bold w-100 border-bottom pb-3 mb-3"></h4>
        <p class="d-flex  flex-wrap align-items-center tabSubTitle  mb-2">
          <span class="text-primary font-weight-bold mr-0">(1) </span>
        </p>
        <input
          type="text"
          :disabled="method.indexOf('view') >=0"
          class="form-control w-75 mb-4"
          disabled
          placeholder="XXX"
        >
        <p class="d-flex  flex-wrap align-items-center tabSubTitle  mb-2">
          <span class="text-primary font-weight-bold mr-0">(2) </span>
        </p>
        <input
          :disabled="method.indexOf('view') >=0"
          type="text"
          class="form-control w-75 mb-4"
          disabled
          placeholder=""
        >
        <p class="d-flex  flex-wrap align-items-center tabSubTitle  mb-2">
          <span class="text-primary font-weight-bold mr-0">(3) </span>
        </p>
        <input
          :disabled="method.indexOf('view') >=0"
          type="text"
          class="form-control w-75 mb-4"
          disabled
          placeholder=""
        >
        <p class="d-flex  flex-wrap align-items-center tabSubTitle  mb-2">
          <span class="text-primary font-weight-bold mr-0">(4) </span>
        </p>
        <input
          :disabled="method.indexOf('view') >=0"
          type="text"
          class="form-control w-75 mb-4"
          disabled
          placeholder="12345678"
        >
        <p class="d-flex  flex-wrap align-items-center tabSubTitle  mb-2">
          <span class="text-primary font-weight-bold mr-0">(5) </span>
        </p>
        <textarea
          :disabled="method.indexOf('view') >=0"
          type="text"
          class="form-control w-75 mb-4"
          disabled
          placeholder="(); "
        ></textarea>

        <!-- <div class="mb-40">
          <p class="d-flex  flex-wrap align-items-center tabSubTitle  mb-2">
            <span class="text-danger star">*</span>
            <span class="text-primary font-weight-bold mr-0"></span>
          </p>
          <div>
            <textarea v-model="yfp_ticket_trust_information"></textarea>
          </div>
          <div>
            <button
              @click="yfp_ticket_trust_information = old_yfp_ticket_trust_information"
              type="button"
              class="btn btn-log mt-3 text-center btn-size font-weight-bold mr-3"
            >
              <div class="d-flex justify-content-center align-items-center">
                <img
                  src="../../assets/img/back_to_default.png"
                  alt=""
                  class="mr-3"
                >
                <span class="text-light"></span>
              </div>
            </button>
          </div>
        </div> -->
        <!--  -->
        <p class="d-flex  flex-wrap align-items-center tabSubTitle  mb-2">
          <span class="text-primary font-weight-bold mr-0"></span>
        </p>
        <input
          type="text"
          class="form-control w-50 mb-8"
          :placeholder="mon+''"
          disabled
        >
      </div>
      <!--  -->
      <div
        class="tab-pane fade px-30 pt-30"
        :class="{'show active' : tab === ''}"
        id="returnNotice"
        role="tabpanel"
      >
        <h4 class="font-weight-bold w-100 border-bottom pb-3 mb-4"></h4>
        <p class="d-flex align-items-center tabSubTitle  mb-1">
          <span class="text-danger star">*</span>
          <label
            class="text-primary font-weight-bold mr-3 mb-0"
            for="yfp_ticket_return_information"
          ></label>
        <div>
            <textarea
              :disabled="method.indexOf('view') >=0"
              class="form-control w-50"
              required
              id="yfp_ticket_return_information"
              rows="10"
              v-model="yfp_ticket_return_information"
            ></textarea>
        </div>
        <div>
          <button
            @click="yfp_ticket_return_information = old_yfp_ticket_return_information"
            type="button"
            class="btn btn-log mt-3 text-center btn-size font-weight-bold mr-3 d-flex align-items-center justify-content-center"
          >
            <div class="d-flex justify-content-center align-items-center">
              <img
                src="../../assets/img/back_to_default.png"
                alt=""
                class="mr-3"
              >
              <span class="text-light"></span>
            </div>
          </button>
        </div>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapActions, mapState} from "vuex";
  import {mapFields} from "vuex-map-fields";
  import Multiselect from 'vue-multiselect'

  export default {
    components: {Multiselect},
    data() {
      return {
        tab: "",
        value: null,
        method: 'view',
      };
    },
    methods: {
      ...mapActions("trustTicketPage", ["initForm", "resize", "post"]),
      ...mapActions("reviewTrustTicket", ["pass", "reject"]),
      ...mapActions("ui", ["resetErrMsg"]),
      calculatePrice(k) {
        this.priceDiscount = parseFloat(this.priceDiscount);
        this.yfp_ticket_base_price = this.yfp_ticket_base_price >> 0;
        this.yfp_ticket_price = this.yfp_ticket_price >> 0;
        if (this.priceDiscount > 100 || this.priceDiscount <= 0)
          this.priceDiscount = 100;
        if (k === "yfp_ticket_base_price" || k === "priceDiscount") {
          this.yfp_ticket_price = Math.floor(
            (this.yfp_ticket_base_price * this.priceDiscount) / 100
          );
        } else {
          this.yfp_ticket_base_price = Math.floor(
            (this.yfp_ticket_price * 100) / this.priceDiscount
          );
        }
      },
      triggerFile(key) {
        this.$refs[key].click();
      },
      resetFile(key, image) {
        this.$refs[key].value = "";
        this[image] = "";
      }
    },
    computed: {
      ...mapState("ui", ["errMsg"]),
      ...mapState("trustTicketPage", ["branch"]),
      ...mapFields("trustTicketPage", [
        "id",
        "franchiseShow",
        "lastUpdate",
        "yfp_ticket_name",
        "yfp_ticket_sub_title",
        "yfp_ticket_address",
        "yfp_ticket_branch_url",
        "yfp_ticket_content",
        "yfp_ticket_num",
        "yfp_ticket_start_date",
        "yfp_ticket_end_date",
        "yfp_event_num",
        "yfp_ticket_order",
        "yfp_ticket_show_sell_information",
        "yfp_ticket_category",
        "yfp_ticket_base_price",
        "priceDiscount",
        "yfp_ticket_price",
        "category_options",
        "yfp_ticket_image",
        "mon",
        "yfp_ticket_trust_information",
        "yfp_ticket_return_information",
        "yfp_ticket_use_description",
        "yfp_ticket_notice_description",
        "yfp_ticket_store_description",
        "yfp_ticket_description_image",
        "old_yfp_ticket_trust_information",
        "old_yfp_ticket_return_information",
        "old_yfp_ticket_content",
        "old_yfp_ticket_use_description",
        "old_yfp_ticket_notice_description",
        "old_yfp_ticket_store_description",
        "yfp_ticket_branch"
      ])
    },
    created() {
      this.resetErrMsg();
      this.initForm();
    }
  };
</script>
<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
<style lang="scss">
  body {
    background: #ffffff;
  }
  .saveBtnSpan {
    // font-size: 31px;
    line-height: 31px;
    margin-top: 2px;
    display: flex;
    align-items: center;
    vertical-align: sub;
    box-sizing: border-box;
  }

  .alert {
    padding: 20px;
    background-color: #f44336; /* Red */
    color: white;
    margin-bottom: 15px;
  }

  .fzProductName {
    font-size: 14px;
    color: rgba(75, 75, 75, 0.5);
  }

  /* The close button */
  .closebtn {
    margin-left: 15px;
    color: white;
    font-weight: bold;
    float: right;
    font-size: 22px;
    line-height: 20px;
    cursor: pointer;
    transition: 0.3s;
  }

  /* When moving the mouse over the close button */
  .closebtn:hover {
    color: black;
  }

  .rightContentDetailTitle {
    color: #3b44a9;
    font-size: 212%;
    margin-bottom: 2%;
    vertical-align: center;
  }

  // button
  .addNewTicketPage {
    .btn-save,
    .btn-finish {
      width: 134px;
      height: 52px;
      border-radius: 26px;
      padding-top: 6px;
      padding-left: 19px;
      box-sizing: border-box;
    }
  }

  // 
  .nav-tabs {
    border-bottom: 0px;
    margin-top: 55px;

    .nav-link {
      &.prodInform {
        border-color: #dee2e6 #dee2e6 transparent;
        border-width: 2px 2px 0px 2px;

        &:hover {
          border-color: #3b44a9 #3b44a9;
          border-width: 2px 2px 0px 2px;
        }
      }

      &.trustInfo {
        border-color: #dee2e6 #dee2e6 transparent;
        border-width: 2px 2px 0px 0px;

        &:hover {
          border-color: #3b44a9 #3b44a9 #dee2e6;
          border-width: 2px 2px 0px;
        }
      }

      &.returnNotice {
        border-color: #dee2e6 #dee2e6 transparent;
        border-width: 2px 2px 0px 0px;

        &:hover {
          border-color: #3b44a9 #3b44a9 #dee2e6;
          border-width: 2px 2px 0px;
        }
      }
    }

    .nav-item {
      margin-bottom: 0px;
    }
  }

  // 
  .nav-tabs .nav-link.active {
    border-color: #3b44a9 #3b44a9 #dee2e6;
    position: relative;
    z-index: 1000;
    border-width: 2px 2px 0px;
  }

  // 
  .active {
    h4 {
      margin-bottom: 1rem;
    }

    // 
    .intro {
      margin-top: 40px;
      width: 55%;
      // border-bottom: 2px solid #c9c9c9;
      box-sizing: border-box;

      .star {
        width: 20px;
      }

      img[alt="upload"] {
        cursor: pointer;
      }

      .cameraImgBox {
        width: 340px;
        height: 230px;
        box-sizing: border-box;
        background: rgba(201, 201, 201, 0.16);
        cursor: pointer;
        // background-image: url("../assets/logo.png");
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
      }

      .imgRestrict {
        font-size: 14px;
        color: rgba(75, 75, 75, 0.5);
      }

      .btn-size {
        width: 144px;
        border-radius: 3px;
        box-sizing: border-box;

        span {
          font-size: 16px;
          line-height: 16px;
          // margin-top: 2px;
        }
      }

      img[alt="plugin"] {
        width: 100%;
      }
    }

    .tabSubTitle {
      font-size: 20px;
    }

    .star {
      width: 20px;
    }

    // 
    .priceNum {
      box-sizing: border-box;
      margin-top: 40px;
      width: 35%;
      margin-left: 10%;

      .fzProductName {
        font-size: 14px;
        color: rgba(75, 75, 75, 0.5);
      }

      .tabSubTitle {
        font-size: 20px;
      }

      .tabSubSubTitle {
        font-size: 14px;
      }

      .star {
        width: 20px;
      }

      .priceSetting {
        width: 100%;
        box-sizing: border-box;
        padding: 16px;
        border: 2px solid #9fc4f5;
        border-radius: 5px;

        .highlight {
          background: rgba(201, 201, 201, 0.17);
          border-radius: 5px;

          .enterDiscount {
            font-size: 14px;
          }

          .enterDiscountDes {
            font-size: 13px;
          }
        }
      }
    }
  }

  .tab-content {
    border-left: 2px solid #3b44a9;
    border-right: 2px solid #3b44a9;
    border-bottom: 2px solid #3b44a9;
    border-top: 2px solid #3b44a9;
    margin-bottom: 16px;
    margin-top: -2px;
    position: relative;
    z-index: 100;
  }

  //
  #returnNotice {
    .btn-size {
      width: 144px;
      border-radius: 3px;
      box-sizing: border-box;

      span {
        font-size: 16px;
        line-height: 16px;
        // margin-top: 2px;
      }
    }
  }

  // 
  input#yfp_ticket_start_date,
  input#yfp_ticket_end_date {
    height: calc(1.5em + 0.75rem);
    padding-left: 1rem;
    border: 0;
    width: 100%;
  }
</style>



